
import '../App.css';

export default function Item({item, chooseItem}) 
{ 
    const img_object = 'http://steamcommunity-a.akamaihd.net/economy/image/'
   return (
    <div onClick={()=>chooseItem(item.id)} className={item.checked ? 'item checked' : 'item'}>
        <span className='item-price'>{item.price} ₽</span>
        <img className='icon' alt="dsdsd" src={img_object + item.icon_url}/>
        <span className='item-name'>{item.market_name}</span>
    </div>
   );
}