import React, { useEffect, useMemo, useState } from 'react';
import Helpmodal from '../components/helpmodal';
import Feedback from '../components/allcomments';
import axios from 'axios';

function Help({account, default_link_help}) 
{
  const [getFeedback, setGetFeedback] = useState([])
  const [page, setPage] = useState(1);
  const feedback = useMemo(()=>getFeedback.filter((item, id)=> Math.ceil((id+1)/3) == page),[page, getFeedback]);
  const handleChange = (event, value) => {
    setPage(value);
};
  useEffect(async() => 
  {
    setGetFeedback((await axios.get(`${default_link_help}api/send-tech-help/`, {withCredentials:true})).data)
  }, [])
  
  return (
      <>
    <div className='review'>
    <Helpmodal response={account} base_url={default_link_help}></Helpmodal>
      <div className='feedback-list'>
          {feedback.map(item=> <Feedback item={item}></Feedback> )}
      </div>
    </div>
  
    </>
  );

}

export default Help;